/* src/components/Dice.css */
.dice-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  position: relative;
}

.die {
  width: 100px;
  height: 100px;
  transform-style: preserve-3d;
  transition: transform 2s;
  position: relative;
  margin: 20px;
}

.die.rolling {
  animation: roll 2s infinite;
}

@keyframes roll {
  0% { transform: rotateX(0deg) rotateY(0deg); }
  100% { transform: rotateX(360deg) rotateY(360deg); }
}

.face {
  position: absolute;
  width: 100px;
  height: 100px;
  background: #ffcc00;
  border: 1px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  transform-style: preserve-3d;
}

.d20 .face {
  width: 50px;
  height: 50px;
}

.d20 .face-1 { transform: rotateX(90deg) translateZ(50px); }
.d20 .face-2 { transform: rotateX(-90deg) translateZ(50px); }
.d20 .face-3 { transform: rotateY(0deg) translateZ(50px); }
.d20 .face-4 { transform: rotateY(180deg) translateZ(50px); }
.d20 .face-5 { transform: rotateY(90deg) translateZ(50px); }
.d20 .face-6 { transform: rotateY(-90deg) translateZ(50px); }
.d20 .face-7 { transform: rotateY(45deg) translateZ(50px); }
.d20 .face-8 { transform: rotateY(-45deg) translateZ(50px); }
.d20 .face-9 { transform: rotateY(135deg) translateZ(50px); }
.d20 .face-10 { transform: rotateY(-135deg) translateZ(50px); }
.d20 .face-11 { transform: rotateX(45deg) translateZ(50px); }
.d20 .face-12 { transform: rotateX(-45deg) translateZ(50px); }
.d20 .face-13 { transform: rotateX(135deg) translateZ(50px); }
.d20 .face-14 { transform: rotateX(-135deg) translateZ(50px); }
.d20 .face-15 { transform: rotateX(90deg) rotateY(45deg) translateZ(50px); }
.d20 .face-16 { transform: rotateX(90deg) rotateY(-45deg) translateZ(50px); }
.d20 .face-17 { transform: rotateX(-90deg) rotateY(45deg) translateZ(50px); }
.d20 .face-18 { transform: rotateX(-90deg) rotateY(-45deg) translateZ(50px); }
.d20 .face-19 { transform: rotateY(45deg) rotateZ(90deg) translateZ(50px); }
.d20 .face-20 { transform: rotateY(-45deg) rotateZ(90deg) translateZ(50px); }

.roll-button {
  margin-top: 30px;
}
